html {
  --background-color: #f3f3f3;
  --primary-color: #383838;
  --secondary-color: #fafaf6;
  --text-color-primary: #404040;
  --text-color-secondary: #fafaf6;
  --sidebar-width: 350px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #404040;
  color: var(--text-color-primary);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  padding-left: 350px;
  padding-left: var(--sidebar-width);
}


.home_title {
    color: red;
}

.home_container {
    background-color: var(--background-color);
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    overflow-y: scroll;
}

.home_card {
    background-color: white;
    width: 350px;
    height: 400px;
    margin: 8px;
}

.home_card>img {
    width: 100%;
}

.home_card p{
    padding: 0 8px;
    text-align: justify;
}

.home_card h1{
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 4px;
}

.home_card h2{
    font-size: 0.9rem;
    font-weight: 250;
    font-style: italic;
}

.avatar {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-right: 8px;
}

.title_container{
    display: flex;
    padding: 8px;
}

.title_box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 1;
}

.trash_icon{
    width: 25px;
    height: 25px;
    padding: 8px;
    border-radius: 50%;
    transition: 0.2s;
    cursor: pointer;
}

.trash_icon:hover{
    background-color: rgba(0, 0, 0, 0.7);
}

.menu_title {
    font-weight: 800;
}

.sidebar {
    background-color: var(--primary-color);
    height: 100vh;
    width: var(--sidebar-width);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    color: var(--text-color-secondary);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.sidebar>.item{
    position: relative;
    font-size: 25px;
    padding: 8px;
    font-weight: 600;
    cursor: pointer;
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;
    text-align: center;
    transition: all 0.4s;
}

.sidebar>.item::before{
    content: '';
    position: absolute;
    background-color: var(--secondary-color);
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%;
    z-index: -1;
    transition: 0.4s;
}

.sidebar>.item:hover::before{
    left: 0;
}
.sidebar>.item:hover{
    color: var(--primary-color);
}

.sidebar>img{
    width: 200px;
}
.form_container {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

.form_container>h1 {
    font-size: 2rem;
    font-weight: 800;
}

.form_container>form {
    display: flex;
    flex-direction: column;
    padding: 8px;
    width: 600px;
    margin: auto
}

.form_container input {
    flex-grow: 1;
    margin-left:  8px;
    padding: 8px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.form_container label {
    width: 75px;;
}

.form_container button {
    width: 100px;
    margin-left: auto;
    margin-right: 8px;
    padding: 8px;
    border-radius: 5px;
    color: var(--primary-color);
    background-color: var(--text-color-secondary);
    transition: all 0.4s;
}

.form_group {
    display: flex;
    padding: 8px;
    align-items: baseline;
}

.form_group> input {
    flex-grow: 1;
    margin-left:  8px;
    padding: 8px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.form_group> label {
    width: 75px;
}

.form_container button:hover {
    background-color: var(--primary-color);
    color: var(--text-color-secondary);
}
