
.home_title {
    color: red;
}

.home_container {
    background-color: var(--background-color);
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    overflow-y: scroll;
}

.home_card {
    background-color: white;
    width: 350px;
    height: 400px;
    margin: 8px;
}

.home_card>img {
    width: 100%;
}

.home_card p{
    padding: 0 8px;
    text-align: justify;
}

.home_card h1{
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 4px;
}

.home_card h2{
    font-size: 0.9rem;
    font-weight: 250;
    font-style: italic;
}

.avatar {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-right: 8px;
}

.title_container{
    display: flex;
    padding: 8px;
}

.title_box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
}

.trash_icon{
    width: 25px;
    height: 25px;
    padding: 8px;
    border-radius: 50%;
    transition: 0.2s;
    cursor: pointer;
}

.trash_icon:hover{
    background-color: rgba(0, 0, 0, 0.7);
}